import { html } from 'lit';
import { ENGINES, SIGNAL_CONNECTIONS, ZONE_COLORS, type EngineNode } from './constants.ts';

type EngineStatus = { id: string; active: boolean; load?: number };
type Signal = { from: string; to: string; active: boolean };

export function renderBrainMap(
  cognition: { selfState?: { valence?: number } } | null,
  engineStatuses?: EngineStatus[],
  recentSignals?: Signal[],
) {
  const valence = cognition?.selfState?.valence ?? 0;
  const bgHue = 200 + valence * 30;
  const statusMap = new Map((engineStatuses ?? []).map(s => [s.id, s]));
  const activeSignals = new Set(
    (recentSignals ?? []).filter(s => s.active).map(s => `${s.from}->${s.to}`)
  );

  const W = 800;
  const H = 500;

  return html`
    <svg viewBox="0 0 ${W} ${H}" class="mind-brain-svg" xmlns="http://www.w3.org/2000/svg">
      <defs>
        <radialGradient id="brain-bg" cx="50%" cy="50%" r="60%">
          <stop offset="0%" stop-color="hsl(${bgHue}, 40%, 8%)" />
          <stop offset="100%" stop-color="#030308" />
        </radialGradient>
        <filter id="glow">
          <feGaussianBlur stdDeviation="3" result="blur" />
          <feMerge><feMergeNode in="blur" /><feMergeNode in="SourceGraphic" /></feMerge>
        </filter>
      </defs>
      <rect width="${W}" height="${H}" fill="url(#brain-bg)" rx="12" />

      <!-- Zone labels -->
      <text x="60" y="30" fill="${ZONE_COLORS.outer}" font-size="10" font-family="var(--mono)" opacity="0.5">OUTER</text>
      <text x="310" y="30" fill="${ZONE_COLORS.thalamus}" font-size="10" font-family="var(--mono)" opacity="0.5">THALAMUS</text>
      <text x="530" y="30" fill="${ZONE_COLORS.inner}" font-size="10" font-family="var(--mono)" opacity="0.5">INNER</text>
      <text x="670" y="30" fill="${ZONE_COLORS.body}" font-size="10" font-family="var(--mono)" opacity="0.5">BODY</text>

      <!-- Signal lines -->
      ${SIGNAL_CONNECTIONS.map(([fromId, toId]) => {
        const from = ENGINES.find(e => e.id === fromId);
        const to = ENGINES.find(e => e.id === toId);
        if (!from || !to) return '';
        const x1 = from.x * W;
        const y1 = from.y * H;
        const x2 = to.x * W;
        const y2 = to.y * H;
        const isActive = activeSignals.has(`${fromId}->${toId}`);
        const opacity = isActive ? 0.8 : 0.08;
        const dash = isActive ? 'none' : '2,4';
        const color = isActive ? ZONE_COLORS[to.zone] : 'rgba(255,255,255,0.3)';
        return html`
          <line x1="${x1}" y1="${y1}" x2="${x2}" y2="${y2}"
                stroke="${color}" stroke-width="${isActive ? 1.5 : 0.5}"
                stroke-dasharray="${dash}" opacity="${opacity}" />
          ${isActive ? html`
            <circle r="2.5" fill="${color}" opacity="0.9">
              <animateMotion dur="0.6s" repeatCount="indefinite"
                path="M${x1},${y1} L${x2},${y2}" />
            </circle>
          ` : ''}
        `;
      })}

      <!-- Engine nodes -->
      ${ENGINES.map(engine => {
        const cx = engine.x * W;
        const cy = engine.y * H;
        const color = ZONE_COLORS[engine.zone];
        const status = statusMap.get(engine.id);
        const isActive = status?.active ?? false;
        const nodeOpacity = isActive ? 1 : 0.4;
        return html`
          <g filter="${isActive ? 'url(#glow)' : ''}">
            <!-- Outer glow -->
            <circle cx="${cx}" cy="${cy}" r="18"
                    fill="none" stroke="${color}" stroke-width="1"
                    opacity="${nodeOpacity * 0.4}" />
            <!-- Core -->
            <circle cx="${cx}" cy="${cy}" r="14"
                    fill="${color}" fill-opacity="0.12"
                    stroke="${color}" stroke-width="${isActive ? 1.5 : 0.5}"
                    opacity="${nodeOpacity}" />
            ${isActive ? html`
              <circle cx="${cx}" cy="${cy}" r="4" fill="${color}" opacity="0.8">
                <animate attributeName="r" values="3;5;3" dur="2s" repeatCount="indefinite" />
                <animate attributeName="opacity" values="0.8;0.4;0.8" dur="2s" repeatCount="indefinite" />
              </circle>
            ` : ''}
            <!-- Label -->
            <text x="${cx}" y="${cy + 28}" text-anchor="middle"
                  fill="rgba(255,255,255,${nodeOpacity * 0.7})"
                  font-size="8" font-family="var(--mono)">
              ${engine.label}
            </text>
          </g>
        `;
      })}
    </svg>
  `;
}
