import { html, nothing } from "lit";
import type { EventLogEntry } from "../app-events.ts";

export type ActivityProps = {
  eventLog: EventLogEntry[];
  activityFilter: string;
  activityAgentFilter: string | null;
  assistantName: string;
  assistantAvatar: string | null;
  onFilterChange: (filter: string) => void;
  onAgentFilterChange: (agentId: string | null) => void;
};

function formatRelativeTime(timestamp: number): string {
  const diff = Date.now() - timestamp;
  if (diff < 60_000) return "just now";
  if (diff < 3_600_000) return `${Math.floor(diff / 60_000)} min ago`;
  if (diff < 86_400_000) return `${Math.floor(diff / 3_600_000)}h ago`;
  return new Date(timestamp).toLocaleString();
}

function eventTypeLabel(entry: EventLogEntry): string {
  const rec = entry as Record<string, unknown>;
  const type = (rec.type ?? rec.method ?? "") as string;
  if (type.includes("message") || type.includes("chat")) return "message";
  if (type.includes("tool")) return "tool";
  if (type.includes("cron")) return "cron";
  if (type.includes("channel")) return "channel";
  if (type.includes("connect")) return "system";
  if (type.includes("disconnect")) return "system";
  return "system";
}

function describeEvent(entry: EventLogEntry): string {
  const rec = entry as Record<string, unknown>;
  const type = (rec.type ?? rec.method ?? "") as string;
  const channel = rec.channel as string | undefined;
  const tool = rec.toolName as string | undefined;
  const name = rec.name as string | undefined;

  if (type.includes("chat.message") || type.includes("message.send")) return "sent a message";
  if (type.includes("message.receive") || type.includes("message.in")) return "received a message";
  if (type.includes("tool.call")) return `used ${tool ?? name ?? "a tool"}`;
  if (type.includes("tool.result")) return `tool ${tool ?? name ?? ""} returned result`;
  if (type.includes("cron.run")) return `ran scheduled job: ${name ?? ""}`;
  if (type.includes("cron.skip")) return `skipped scheduled job: ${name ?? ""}`;
  if (type.includes("channel.send")) return `posted to ${channel ?? "a channel"}`;
  if (type.includes("channel.receive")) return `received from ${channel ?? "a channel"}`;
  if (type.includes("connect")) return "connected to gateway";
  if (type.includes("disconnect")) return "disconnected from gateway";
  if (type.includes("session")) return "session updated";
  if (type.includes("config")) return "configuration changed";
  return type || "event occurred";
}

function badgeClass(typeLabel: string): string {
  switch (typeLabel) {
    case "message":
      return "chip-ok";
    case "tool":
      return "chip-warn";
    case "cron":
      return "";
    case "channel":
      return "chip-ok";
    case "system":
      return "";
    default:
      return "";
  }
}

export function renderActivity(props: ActivityProps) {
  const filtered = props.eventLog.filter((entry) => {
    if (props.activityFilter && props.activityFilter !== "all") {
      if (eventTypeLabel(entry) !== props.activityFilter) return false;
    }
    return true;
  });
  const sorted = [...filtered].reverse();

  return html`
    <div class="activity-view">
      <!-- Filters -->
      <div class="activity-filters">
        <div class="field">
          <span>Event Type</span>
          <select
            .value=${props.activityFilter || "all"}
            @change=${(e: Event) => {
              props.onFilterChange((e.target as HTMLSelectElement).value);
            }}
          >
            <option value="all">All events</option>
            <option value="message">Messages</option>
            <option value="tool">Tools</option>
            <option value="cron">Cron</option>
            <option value="channel">Channels</option>
            <option value="system">System</option>
          </select>
        </div>
      </div>

      <!-- Feed -->
      <div class="activity-feed">
        ${sorted.length === 0
          ? html`<div class="activity-empty muted">No events to display.</div>`
          : sorted.map((entry) => {
              const typeLabel = eventTypeLabel(entry);
              const rec = entry as Record<string, unknown>;
              const channel = rec.channel as string | undefined;
              return html`
                <div class="activity-card">
                  <div class="activity-card__avatar agent-avatar">
                    ${props.assistantAvatar
                      ? html`<img src=${props.assistantAvatar} alt="" />`
                      : props.assistantName.charAt(0).toUpperCase()}
                  </div>
                  <div class="activity-card__body">
                    <div class="activity-card__header">
                      <span class="activity-card__name">${props.assistantName}</span>
                      <span class="activity-card__desc">${describeEvent(entry)}</span>
                    </div>
                    <div class="activity-card__footer">
                      <span class="chip ${badgeClass(typeLabel)}">${typeLabel}</span>
                      ${channel
                        ? html`<span class="chip">${channel}</span>`
                        : nothing}
                      <span class="activity-card__time muted">
                        ${formatRelativeTime(entry.timestamp)}
                      </span>
                    </div>
                  </div>
                </div>
              `;
            })}
      </div>
    </div>
  `;
}
