/**
 * Voice tab view  real-time voice conversation via WebSocket to the
 * Claudius voice backend (Python on ws://localhost:8765).
 *
 * Captures mic audio via AudioWorklet, streams PCM frames, displays
 * transcriptions & responses, and uses browser SpeechSynthesis for TTS.
 */

import { html, nothing, type TemplateResult } from "lit";

//  Types 

export type VoiceState = "IDLE" | "LISTENING" | "PROCESSING" | "SPEAKING";

export type VoiceMessage = {
  role: "user" | "assistant";
  text: string;
  meta?: string;
  streaming?: boolean;
};

export type VoiceProps = {
  active: boolean;
  state: VoiceState;
  messages: VoiceMessage[];
  openclawConnected: boolean;
  onToggleMic: () => void;
};

//  Module-level audio/WS state (singleton per session) 

let ws: WebSocket | null = null;
let audioCtx: AudioContext | null = null;
let processor: AudioWorkletNode | null = null;
let micStream: MediaStream | null = null;
let ttsQueue: string[] = [];
let isSpeaking = false;
let reconnectTimer: number | null = null;

// Callbacks into the Lit app state
let onStateChange: ((state: VoiceState) => void) | null = null;
let onOpenclawStatus: ((connected: boolean) => void) | null = null;
let onUserMessage: ((text: string, meta: string) => void) | null = null;
let onAssistantStart: (() => void) | null = null;
let onAssistantDelta: ((text: string) => void) | null = null;
let onAssistantEnd: ((text: string) => void) | null = null;
let onVad: ((speaking: boolean) => void) | null = null;

//  WebSocket connection 

function connectWs() {
  if (ws && (ws.readyState === WebSocket.OPEN || ws.readyState === WebSocket.CONNECTING)) {
    return;
  }

  ws = new WebSocket("ws://localhost:8765");
  ws.binaryType = "arraybuffer";

  ws.onopen = () => {
    console.log("[voice-ws] connected");
    if (reconnectTimer != null) {
      clearTimeout(reconnectTimer);
      reconnectTimer = null;
    }
  };

  ws.onmessage = (event) => {
    if (typeof event.data === "string") {
      handleServerEvent(JSON.parse(event.data));
    }
  };

  ws.onclose = () => {
    console.log("[voice-ws] disconnected");
    ws = null;
    scheduleReconnect();
  };

  ws.onerror = () => ws?.close();
}

function scheduleReconnect() {
  if (reconnectTimer != null) return;
  reconnectTimer = window.setTimeout(() => {
    reconnectTimer = null;
    connectWs();
  }, 3000);
}

function sendJSON(data: Record<string, unknown>) {
  if (ws?.readyState === WebSocket.OPEN) {
    ws.send(JSON.stringify(data));
  }
}

//  Server event handler 

function handleServerEvent(event: Record<string, unknown>) {
  switch (event.type) {
    case "state":
      onStateChange?.(event.state as VoiceState);
      break;
    case "openclaw_status":
      onOpenclawStatus?.(event.connected as boolean);
      break;
    case "vad":
      onVad?.(event.speaking as boolean);
      break;
    case "transcription": {
      const lang = (event.language as string || "").toUpperCase();
      const dur = event.audio_duration as number;
      const lat = event.latency_ms as number;
      const meta = `${lang} \u00b7 ${dur}s \u00b7 ${Math.round(lat)}ms`;
      onUserMessage?.(event.text as string, meta);
      break;
    }
    case "response_start":
      onAssistantStart?.();
      break;
    case "response_delta":
      onAssistantDelta?.(event.text as string);
      break;
    case "response_end":
      onAssistantEnd?.(event.text as string);
      break;
    case "tts_sentence":
      queueTTS(event.text as string);
      break;
    case "error":
      console.error("[voice-server]", event.message);
      break;
  }
}

//  Audio capture 

async function startAudio() {
  if (!audioCtx) {
    audioCtx = new AudioContext({ sampleRate: 16000 });
    await audioCtx.audioWorklet.addModule("/audio-processor.js");
  }

  if (audioCtx.state === "suspended") {
    await audioCtx.resume();
  }

  micStream = await navigator.mediaDevices.getUserMedia({
    audio: {
      echoCancellation: true,
      noiseSuppression: true,
      autoGainControl: true,
    },
  });

  const source = audioCtx.createMediaStreamSource(micStream);
  processor = new AudioWorkletNode(audioCtx, "audio-capture");

  processor.port.onmessage = (e: MessageEvent<ArrayBuffer>) => {
    if (ws?.readyState === WebSocket.OPEN) {
      ws.send(e.data);
    }
  };

  source.connect(processor);
}

function stopAudio() {
  processor?.disconnect();
  processor = null;
  micStream?.getTracks().forEach((t) => t.stop());
  micStream = null;
}

//  Browser TTS 

function queueTTS(text: string) {
  ttsQueue.push(text);
  if (!isSpeaking) {
    speakNext();
  }
}

function speakNext() {
  if (ttsQueue.length === 0) {
    isSpeaking = false;
    sendJSON({ type: "tts_done" });
    return;
  }

  isSpeaking = true;
  const text = ttsQueue.shift()!;
  const utterance = new SpeechSynthesisUtterance(text);
  utterance.rate = 1.0;
  utterance.onend = () => speakNext();
  utterance.onerror = () => speakNext();
  speechSynthesis.speak(utterance);
}

//  Public API for the Lit component 

export function initVoice(callbacks: {
  onStateChange: (state: VoiceState) => void;
  onOpenclawStatus: (connected: boolean) => void;
  onUserMessage: (text: string, meta: string) => void;
  onAssistantStart: () => void;
  onAssistantDelta: (text: string) => void;
  onAssistantEnd: (text: string) => void;
  onVad: (speaking: boolean) => void;
}) {
  onStateChange = callbacks.onStateChange;
  onOpenclawStatus = callbacks.onOpenclawStatus;
  onUserMessage = callbacks.onUserMessage;
  onAssistantStart = callbacks.onAssistantStart;
  onAssistantDelta = callbacks.onAssistantDelta;
  onAssistantEnd = callbacks.onAssistantEnd;
  onVad = callbacks.onVad;
  connectWs();
}

export function teardownVoice() {
  onStateChange = null;
  onOpenclawStatus = null;
  onUserMessage = null;
  onAssistantStart = null;
  onAssistantDelta = null;
  onAssistantEnd = null;
  onVad = null;

  if (reconnectTimer != null) {
    clearTimeout(reconnectTimer);
    reconnectTimer = null;
  }
}

export async function toggleMic(active: boolean): Promise<boolean> {
  if (active) {
    // Stop
    speechSynthesis.cancel();
    ttsQueue = [];
    isSpeaking = false;
    stopAudio();
    sendJSON({ type: "stop" });
    return false;
  } else {
    // Start
    await startAudio();
    sendJSON({ type: "start" });
    return true;
  }
}

//  Render 

function escapeHtml(text: string): string {
  const div = document.createElement("div");
  div.textContent = text;
  return div.innerHTML;
}

function renderVoiceMessage(msg: VoiceMessage, index: number): TemplateResult {
  const cls = `voice-msg voice-msg--${msg.role}${msg.streaming ? " voice-msg--streaming" : ""}`;
  return html`
    <div class=${cls} data-index=${index}>
      <div class="voice-msg__text">${msg.streaming ? html`${msg.text}<span class="voice-cursor"></span>` : msg.text}</div>
      ${msg.meta ? html`<div class="voice-msg__meta">${msg.meta}</div>` : nothing}
    </div>
  `;
}

export function renderVoice(props: VoiceProps): TemplateResult {
  const stateClass = props.state.toLowerCase();

  return html`
    <section class="card voice-view">
      <div class="voice-header">
        <div class="voice-status">
          <span class="voice-badge voice-badge--${stateClass}">${props.state}</span>
          <span class="voice-badge voice-badge--openclaw ${props.openclawConnected ? "voice-badge--ok" : "voice-badge--off"}">
            OpenClaw ${props.openclawConnected ? "Connected" : "Offline"}
          </span>
        </div>
      </div>

      <div class="voice-messages" id="voice-messages">
        ${props.messages.length === 0
          ? html`<div class="voice-empty">Click the microphone to start a voice conversation.</div>`
          : props.messages.map((msg, i) => renderVoiceMessage(msg, i))}
      </div>

      <div class="voice-controls">
        <button
          class="voice-mic ${props.active ? "voice-mic--active" : ""}"
          @click=${props.onToggleMic}
          title=${props.active ? "Stop listening" : "Start listening"}
          aria-label=${props.active ? "Stop listening" : "Start listening"}
        >
          <svg viewBox="0 0 24 24" class="voice-mic__icon">
            <path d="M12 2a3 3 0 0 0-3 3v7a3 3 0 0 0 6 0V5a3 3 0 0 0-3-3Z" />
            <path d="M19 10v2a7 7 0 0 1-14 0v-2" />
            <line x1="12" x2="12" y1="19" y2="22" />
          </svg>
        </button>
      </div>
    </section>
  `;
}
