import { html, nothing } from 'lit';
import { FLAVOR_COLORS } from './constants.ts';

export type InnerLifeEntry = {
  id: string;
  flavor: string;
  text: string;
  timestamp: number;
};

export function renderInnerLife(entries: InnerLifeEntry[] | undefined) {
  if (!entries || entries.length === 0) {
    return html`<div class="muted" style="font-size:12px; font-style:italic;">Inner life stream quiet...</div>`;
  }

  const now = Date.now();

  return html`
    <div class="mind-stream">
      ${entries.map(entry => {
        const age = now - entry.timestamp;
        const opacity = Math.max(0.3, 1 - age / 30000);
        const color = FLAVOR_COLORS[entry.flavor] ?? 'rgba(255,255,255,0.5)';
        const isWandering = entry.flavor === 'wandering';

        return html`
          <div class="mind-stream__entry" style="opacity:${opacity}">
            <span class="mind-stream__dot" style="background:${color}"></span>
            <span class="mind-stream__text" style="color:${color}; ${isWandering ? 'font-style:italic;' : ''}">
              ${entry.text}
            </span>
          </div>
        `;
      })}
    </div>
  `;
}
