import { html } from 'lit';

type SelfState = {
  valence: number;
  arousal: number;
  confidence: number;
  energy: number;
  social: number;
  curiosity: number;
};

const DIMENSIONS: { key: keyof SelfState; label: string; color: string; min: number; max: number }[] = [
  { key: 'valence', label: 'Valence', color: '#f59e0b', min: -1, max: 1 },
  { key: 'arousal', label: 'Arousal', color: '#ef4444', min: 0, max: 1 },
  { key: 'confidence', label: 'Confidence', color: '#3b82f6', min: 0, max: 1 },
  { key: 'energy', label: 'Energy', color: '#10b981', min: 0, max: 1 },
  { key: 'social', label: 'Social', color: '#ec4899', min: 0, max: 1 },
  { key: 'curiosity', label: 'Curiosity', color: '#8b5cf6', min: 0, max: 1 },
];

export function renderSelfState(selfState: SelfState | null) {
  if (!selfState) return html`<div class="muted" style="font-size:12px;">No state data</div>`;

  return html`
    <div class="mind-dimensions">
      ${DIMENSIONS.map(dim => {
        const val = selfState[dim.key];
        const range = dim.max - dim.min;
        const pct = Math.max(0, Math.min(100, ((val - dim.min) / range) * 100));
        const opacity = 0.6 + Math.abs(val) * 0.4;
        const isValence = dim.key === 'valence';

        return html`
          <div class="mind-dimension">
            <div class="mind-dimension__header">
              <span class="mind-dimension__label">${dim.label}</span>
              <span class="mind-dimension__value">${val.toFixed(2)}</span>
            </div>
            <div class="mind-dimension__track">
              ${isValence ? html`<div class="mind-dimension__center"></div>` : ''}
              <div class="mind-dimension__fill"
                   style="width:${pct}%; background:${dim.color}; opacity:${opacity}">
              </div>
            </div>
          </div>
        `;
      })}
    </div>
  `;
}
