import { html, nothing } from "lit";
import type { CognitionState } from "../app-view-state.ts";
import { renderBrainMap } from "./mind/brain-map.ts";
import { renderFace } from "./mind/face.ts";
import { renderInnerLife } from "./mind/inner-life.ts";
import { renderSelfState } from "./mind/self-state.ts";
import { renderSignalLog } from "./mind/signal-log.ts";

export type MindProps = {
  loading: boolean;
  cognition: CognitionState;
  blinkOpen?: boolean;
  onRefresh: () => void;
};

function formatRelativeTime(iso: string): string {
  const diff = Date.now() - new Date(iso).getTime();
  const seconds = Math.floor(diff / 1000);
  if (seconds < 60) {
    return "just now";
  }
  const minutes = Math.floor(seconds / 60);
  if (minutes < 60) {
    return `${minutes}m ago`;
  }
  const hours = Math.floor(minutes / 60);
  if (hours < 24) {
    return `${hours}h ago`;
  }
  const days = Math.floor(hours / 24);
  return `${days}d ago`;
}

export function renderMind(props: MindProps) {
  const { cognition, loading, blinkOpen = true } = props;

  if (!cognition && loading) {
    return html`
      <div class="muted" style="padding: 24px">Loading cognitive state...</div>
    `;
  }

  if (!cognition) {
    return html`
      <section class="mind-section" style="max-width: 600px;">
        <div class="mind-section__title">Cognitive State</div>
        <div style="color: rgba(255,255,255,0.5); font-size: 13px; margin-bottom: 12px;">
          Connect to Wybe OS to see the inner world.
        </div>
        <div class="muted" style="font-size: 12px;">
          Not connected. Configure the Wybe OS URL in settings.
        </div>
        <button class="btn" style="margin-top: 12px;" @click=${props.onRefresh}>Retry</button>
      </section>
    `;
  }

  const s = cognition.selfState;
  const engineStatuses = cognition.engineStatuses ?? [];
  const recentSignals = (cognition.recentSignals ?? []).map((sig) => ({
    ...sig,
    active: sig.active ?? true,
  }));
  const innerLife = cognition.innerLifeStream;
  const signalEntries = (cognition.recentSignals ?? []).map((sig) => ({
    id: sig.id,
    from: sig.from,
    to: sig.to,
    type: sig.type,
    timestamp: sig.timestamp,
  }));

  return html`
    <div class="mind-dashboard">
      <!-- Brain Map -->
      <div class="mind-section mind-brain-map">
        <div class="mind-section__title">Neural Map</div>
        ${renderBrainMap(cognition, engineStatuses, recentSignals)}
      </div>

      <!-- Sidebar: Face + State + Signals -->
      <div class="mind-sidebar">
        <div class="mind-section">
          <div class="mind-section__title">Presence</div>
          <div class="mind-face">
            ${renderFace(s, blinkOpen)}
          </div>
          <div style="text-align: center; margin-top: 4px;">
            <span style="font-size: 13px; color: rgba(255,255,255,0.7); text-transform: capitalize;">
              ${cognition.stateDescription}
            </span>
          </div>
        </div>

        <div class="mind-section">
          <div class="mind-section__title">Dimensions</div>
          ${renderSelfState(s)}
        </div>

        <div class="mind-section">
          <div class="mind-section__title">Signals</div>
          ${renderSignalLog(signalEntries)}
        </div>
      </div>

      <!-- Inner Life Stream -->
      <div class="mind-section mind-stream-container">
        <div class="mind-section__title">Inner Life</div>
        ${renderInnerLife(innerLife)}
      </div>

      <!-- Stats row -->
      <div style="display: flex; gap: 16px; grid-column: 1 / -1;">
        <div class="mind-section" style="flex: 1;">
          <div class="mind-section__title">Memories</div>
          <div style="font-size: 24px; font-weight: 600; color: rgba(255,255,255,0.9);">
            ${cognition.memoryCount}
          </div>
          <div class="muted" style="font-size: 11px; margin-top: 2px;">stored experiences</div>
        </div>
        <div class="mind-section" style="flex: 1;">
          <div class="mind-section__title">Last Interaction</div>
          <div style="font-size: 14px; font-weight: 500; color: rgba(255,255,255,0.9);">
            ${cognition.lastInteraction ? formatRelativeTime(cognition.lastInteraction) : "Never"}
          </div>
          <div class="muted" style="font-size: 11px; margin-top: 2px;">most recent exchange</div>
        </div>
        ${
          cognition.tick != null
            ? html`
          <div class="mind-section" style="flex: 1;">
            <div class="mind-section__title">Tick</div>
            <div style="font-size: 24px; font-weight: 600; color: rgba(255,255,255,0.9);">
              ${cognition.tick}
            </div>
            <div class="muted" style="font-size: 11px; margin-top: 2px;">cognitive cycle</div>
          </div>
        `
            : nothing
        }
        <div class="mind-section" style="flex: 0 0 auto;">
          <button class="btn" @click=${props.onRefresh} style="margin-top: 8px;">Refresh</button>
        </div>
      </div>
    </div>
  `;
}
