import { html, nothing } from "lit";
import type { Goal, GoalStatus } from "../controllers/goals-controller.ts";
import type { AgentsListResult } from "../types.ts";
import type { Tab } from "../navigation.ts";

export type GoalsProps = {
  northStar: string;
  northStarProgress: number;
  goals: Goal[];
  goalsLoading: boolean;
  agentsList: AgentsListResult | null;
  onNorthStarChange: (text: string, progress: number) => void;
  onAddGoal: (goal: {
    title: string;
    description: string;
    agentId: string | null;
    dueDate: string | null;
    status: GoalStatus;
    progress: number;
  }) => void;
  onUpdateGoal: (id: string, patch: Partial<Goal>) => void;
  onRemoveGoal: (id: string) => void;
  onNavigate: (tab: Tab) => void;
};

export function renderGoals(props: GoalsProps) {
  const activeGoals = props.goals.filter((g) => g.status !== "completed");
  const completedGoals = props.goals.filter((g) => g.status === "completed");
  const agents = props.agentsList?.agents ?? [];

  return html`
    <div class="goals-view">
      <!-- North Star -->
      <div class="goals-northstar card">
        <div class="goals-northstar__header">
          <div class="stat-label">North Star</div>
        </div>
        <div class="goals-northstar__body">
          <input
            type="text"
            class="goals-northstar__input"
            placeholder="What is the overarching goal?"
            .value=${props.northStar}
            @change=${(e: Event) => {
              const val = (e.target as HTMLInputElement).value;
              props.onNorthStarChange(val, props.northStarProgress);
            }}
          />
          <div class="goals-northstar__progress">
            <label class="muted">Progress</label>
            <input
              type="range"
              min="0"
              max="100"
              .value=${String(props.northStarProgress)}
              @input=${(e: Event) => {
                const val = Number((e.target as HTMLInputElement).value);
                props.onNorthStarChange(props.northStar, val);
              }}
            />
            <span class="muted">${props.northStarProgress}%</span>
          </div>
        </div>
      </div>

      <!-- Goal List -->
      <div class="goals-list">
        ${activeGoals.map((goal) => renderGoalCard(goal, agents, props))}
      </div>

      <!-- Create Goal Form -->
      <div class="goals-form card">
        <div class="card-title">New Goal</div>
        <form
          class="goals-form__fields"
          @submit=${(e: Event) => {
            e.preventDefault();
            const form = e.target as HTMLFormElement;
            const title = (form.querySelector("[name=title]") as HTMLInputElement).value.trim();
            if (!title) return;
            const description = (form.querySelector("[name=description]") as HTMLTextAreaElement).value.trim();
            const agentId = (form.querySelector("[name=agentId]") as HTMLSelectElement).value || null;
            const dueDate = (form.querySelector("[name=dueDate]") as HTMLInputElement).value || null;
            props.onAddGoal({ title, description, agentId, dueDate, status: "active", progress: 0 });
            form.reset();
          }}
        >
          <div class="field">
            <span>Title</span>
            <input type="text" name="title" required placeholder="Goal title" />
          </div>
          <div class="field">
            <span>Description</span>
            <textarea name="description" rows="2" placeholder="What should be achieved?"></textarea>
          </div>
          <div class="goals-form__row">
            <div class="field">
              <span>Agent</span>
              <select name="agentId">
                <option value="">Unassigned</option>
                ${agents.map(
                  (a) => html`<option value=${a.id}>${a.id}</option>`,
                )}
              </select>
            </div>
            <div class="field">
              <span>Due Date</span>
              <input type="date" name="dueDate" />
            </div>
          </div>
          <button type="submit" class="btn primary">Add Goal</button>
        </form>
      </div>

      <!-- Completed Goals -->
      ${completedGoals.length > 0
        ? html`
          <details class="goals-completed">
            <summary class="goals-completed__toggle">
              <span class="stat-label">Completed (${completedGoals.length})</span>
            </summary>
            <div class="goals-list">
              ${completedGoals.map((goal) => renderGoalCard(goal, agents, props))}
            </div>
          </details>
        `
        : nothing}
    </div>
  `;
}

function renderGoalCard(
  goal: Goal,
  agents: Array<{ id: string }>,
  props: GoalsProps,
) {
  return html`
    <div class="goals-card card ${goal.status === "completed" ? "goals-card--completed" : ""}">
      <div class="goals-card__header">
        <div class="goals-card__title">${goal.title}</div>
        <div class="goals-card__actions">
          <select
            class="goals-card__status"
            .value=${goal.status}
            @change=${(e: Event) => {
              props.onUpdateGoal(goal.id, {
                status: (e.target as HTMLSelectElement).value as GoalStatus,
              });
            }}
          >
            <option value="active">Active</option>
            <option value="paused">Paused</option>
            <option value="completed">Completed</option>
          </select>
          <button
            class="btn btn--sm danger"
            @click=${() => props.onRemoveGoal(goal.id)}
          >Remove</button>
        </div>
      </div>
      ${goal.description
        ? html`<div class="goals-card__desc muted">${goal.description}</div>`
        : nothing}
      <div class="goals-card__meta">
        <div class="field">
          <span>Agent</span>
          <select
            .value=${goal.agentId ?? ""}
            @change=${(e: Event) => {
              props.onUpdateGoal(goal.id, {
                agentId: (e.target as HTMLSelectElement).value || null,
              });
            }}
          >
            <option value="">Unassigned</option>
            ${agents.map(
              (a) => html`<option value=${a.id}>${a.id}</option>`,
            )}
          </select>
        </div>
        <div class="field">
          <span>Due Date</span>
          <input
            type="date"
            .value=${goal.dueDate ?? ""}
            @change=${(e: Event) => {
              props.onUpdateGoal(goal.id, {
                dueDate: (e.target as HTMLInputElement).value || null,
              });
            }}
          />
        </div>
        <div class="field">
          <span>Progress: ${goal.progress}%</span>
          <input
            type="range"
            min="0"
            max="100"
            .value=${String(goal.progress)}
            @input=${(e: Event) => {
              props.onUpdateGoal(goal.id, {
                progress: Number((e.target as HTMLInputElement).value),
              });
            }}
          />
        </div>
      </div>
      <div class="goals-card__footer">
        <button
          class="btn btn--sm"
          @click=${() => props.onNavigate("cron")}
        >Add automation</button>
      </div>
    </div>
  `;
}
