/**
 * Voice-in-chat module  connects to the Python voice backend (ws://localhost:8765)
 * for VAD + Whisper transcription, but routes responses through the existing chat
 * gateway connection instead of a separate OpenClaw connection.
 *
 * TTS: Provider-aware via gateway RPC (tts.synthesize) with Web Audio API playback.
 * The active provider (ElevenLabs, OpenAI, local, etc.) is controlled by user settings.
 * Chunks are synthesized as soon as clause boundaries are detected during streaming.
 *
 * Cycle: IDLE  LISTENING  PROCESSING  SPEAKING  LISTENING  ...
 */

import type { GatewayBrowserClient } from "./gateway.ts";

//  Types 

export type ChatVoiceState = "IDLE" | "LISTENING" | "PROCESSING" | "SPEAKING";

export type ChatVoiceCallbacks = {
  onStateChange: (state: ChatVoiceState) => void;
  onTranscription: (text: string) => void;
  onVad: (speaking: boolean) => void;
  onResponseDelta?: (text: string) => void;
  onResponseEnd?: (text: string) => void;
};

//  Module-level state (singleton) 

let ws: WebSocket | null = null;
let audioCtx: AudioContext | null = null;
let processor: AudioWorkletNode | null = null;
let micStream: MediaStream | null = null;
let reconnectTimer: number | null = null;

let cbStateChange: ((state: ChatVoiceState) => void) | null = null;
let cbTranscription: ((text: string) => void) | null = null;
let cbVad: ((speaking: boolean) => void) | null = null;
let cbResponseDelta: ((text: string) => void) | null = null;
let cbResponseEnd: ((text: string) => void) | null = null;

//  Web Audio API playback 

let playbackCtx: AudioContext | null = null;
const audioQueue: AudioBuffer[] = [];
let currentSource: AudioBufferSourceNode | null = null;
let isPlaying = false;
// Track whether we're in a speaking session (between first enqueue and finishSpeaking + drain)
let speakingSession = false;

//  Concurrency limiter for synthesis requests 

const MAX_INFLIGHT = 3;
let inflight = 0;
const synthQueue: Array<() => void> = [];

function acquireSlot(): Promise<void> {
  if (inflight < MAX_INFLIGHT) {
    inflight++;
    return Promise.resolve();
  }
  return new Promise<void>((resolve) => {
    synthQueue.push(() => {
      inflight++;
      resolve();
    });
  });
}

function releaseSlot() {
  inflight--;
  const next = synthQueue.shift();
  if (next) next();
}

//  Audio playback queue 

function playNext() {
  if (audioQueue.length === 0) {
    isPlaying = false;
    if (!speakingSession) {
      // Stream ended and queue drained  signal backend
      sendJSON({ type: "tts_done" });
      cbStateChange?.("LISTENING");
    }
    return;
  }

  isPlaying = true;
  const buffer = audioQueue.shift()!;
  if (!playbackCtx) return;

  const source = playbackCtx.createBufferSource();
  source.buffer = buffer;
  source.connect(playbackCtx.destination);
  source.onended = () => {
    currentSource = null;
    playNext();
  };
  currentSource = source;
  source.start();
}

function enqueueAudio(buffer: AudioBuffer) {
  audioQueue.push(buffer);
  if (!isPlaying) {
    playNext();
  }
}

//  Gateway TTS synthesis 

async function synthesizeSentence(
  text: string,
  client: GatewayBrowserClient,
): Promise<AudioBuffer | null> {
  try {
    const result = await client.request<{
      audio: string;
      format: string;
      provider: string;
      modelId: string;
    }>("tts.synthesize", { text, modelId: "eleven_turbo_v2_5" });

    if (!result?.audio || !playbackCtx) return null;

    // Decode base64  ArrayBuffer  AudioBuffer
    const binaryString = atob(result.audio);
    const bytes = new Uint8Array(binaryString.length);
    for (let i = 0; i < binaryString.length; i++) {
      bytes[i] = binaryString.charCodeAt(i);
    }
    return await playbackCtx.decodeAudioData(bytes.buffer);
  } catch (err) {
    console.error("[chat-voice-tts] synthesis failed:", err);
    return null;
  }
}

//  WebSocket connection 

function connectWs() {
  if (ws && (ws.readyState === WebSocket.OPEN || ws.readyState === WebSocket.CONNECTING)) {
    return;
  }

  ws = new WebSocket("ws://localhost:8765");
  ws.binaryType = "arraybuffer";

  ws.onopen = () => {
    console.log("[chat-voice-ws] connected");
    if (reconnectTimer != null) {
      clearTimeout(reconnectTimer);
      reconnectTimer = null;
    }
  };

  ws.onmessage = (event) => {
    if (typeof event.data === "string") {
      handleServerEvent(JSON.parse(event.data));
    }
    // Binary data (audio frames from server) is ignored  we don't play server audio
  };

  ws.onclose = () => {
    console.log("[chat-voice-ws] disconnected");
    ws = null;
    scheduleReconnect();
  };

  ws.onerror = () => ws?.close();
}

function scheduleReconnect() {
  if (reconnectTimer != null) return;
  reconnectTimer = window.setTimeout(() => {
    reconnectTimer = null;
    connectWs();
  }, 3000);
}

function sendJSON(data: Record<string, unknown>) {
  if (ws?.readyState === WebSocket.OPEN) {
    ws.send(JSON.stringify(data));
  }
}

//  Server event handler 

function handleServerEvent(event: Record<string, unknown>) {
  switch (event.type) {
    case "state":
      // Map server states to our chat voice states.
      // The server sends IDLE/LISTENING/PROCESSING/SPEAKING but we only care
      // about LISTENING and PROCESSING from the server  SPEAKING is driven
      // by browser TTS, not the server.
      {
        const serverState = event.state as string;
        if (serverState === "LISTENING") {
          cbStateChange?.("LISTENING");
        } else if (serverState === "PROCESSING") {
          cbStateChange?.("PROCESSING");
        }
        // Ignore server SPEAKING/IDLE  we manage those ourselves
      }
      break;
    case "vad":
      cbVad?.(event.speaking as boolean);
      break;
    case "transcription":
      // The voice backend transcribed user speech  send it as a chat message
      cbTranscription?.(event.text as string);
      break;
    case "response_delta":
      // Streaming response text from voice server (Claude fallback when gateway unavailable)
      cbResponseDelta?.(event.text as string);
      break;
    case "response_end":
      // Final response from voice server
      cbResponseEnd?.(event.text as string);
      break;
    case "error":
      console.error("[chat-voice-server]", event.message);
      break;
  }
}

//  Audio capture 

async function startAudio() {
  if (!audioCtx) {
    audioCtx = new AudioContext({ sampleRate: 16000 });
    await audioCtx.audioWorklet.addModule("/audio-processor.js");
  }

  if (audioCtx.state === "suspended") {
    await audioCtx.resume();
  }

  micStream = await navigator.mediaDevices.getUserMedia({
    audio: {
      echoCancellation: true,
      noiseSuppression: true,
      autoGainControl: true,
    },
  });

  const source = audioCtx.createMediaStreamSource(micStream);
  processor = new AudioWorkletNode(audioCtx, "audio-capture");

  processor.port.onmessage = (e: MessageEvent<ArrayBuffer>) => {
    if (ws?.readyState === WebSocket.OPEN) {
      ws.send(e.data);
    }
  };

  source.connect(processor);
}

function stopAudio() {
  processor?.disconnect();
  processor = null;
  micStream?.getTracks().forEach((t) => t.stop());
  micStream = null;
}

//  Public API 

export function startChatVoice(callbacks: ChatVoiceCallbacks) {
  cbStateChange = callbacks.onStateChange;
  cbTranscription = callbacks.onTranscription;
  cbVad = callbacks.onVad;
  cbResponseDelta = callbacks.onResponseDelta ?? null;
  cbResponseEnd = callbacks.onResponseEnd ?? null;
  connectWs();
}

export function stopChatVoice() {
  cbStateChange = null;
  cbTranscription = null;
  cbVad = null;
  cbResponseDelta = null;
  cbResponseEnd = null;

  cancelTts();
  stopAudio();

  if (reconnectTimer != null) {
    clearTimeout(reconnectTimer);
    reconnectTimer = null;
  }
}

export async function toggleChatMic(active: boolean): Promise<boolean> {
  if (active) {
    // Currently active  stop
    cancelTts();
    stopAudio();
    sendJSON({ type: "stop" });
    return false;
  } else {
    // Currently inactive  start
    // Ensure playback context is ready
    if (!playbackCtx) {
      playbackCtx = new AudioContext();
    }
    if (playbackCtx.state === "suspended") {
      await playbackCtx.resume();
    }
    await startAudio();
    sendJSON({ type: "start" });
    return true;
  }
}

/**
 * Synthesize a text chunk via ElevenLabs and enqueue for playback.
 * Respects concurrency limit (max 3 in-flight requests).
 */
export async function synthesizeAndEnqueue(
  text: string,
  client: GatewayBrowserClient,
) {
  if (!text.trim()) return;

  // Enter speaking state on first chunk
  cbStateChange?.("SPEAKING");

  await acquireSlot();
  try {
    const buffer = await synthesizeSentence(text, client);
    if (buffer) {
      enqueueAudio(buffer);
    }
  } finally {
    releaseSlot();
  }
}

/**
 * Called when the chat stream ends. Marks speaking session as done
 * so when the audio queue drains, we signal tts_done.
 */
export function finishSpeaking() {
  speakingSession = false;
  // If queue is already empty and nothing is playing, signal immediately
  if (!isPlaying && audioQueue.length === 0) {
    sendJSON({ type: "tts_done" });
    cbStateChange?.("LISTENING");
  }
}

/**
 * Start a new speaking session  called when a new stream begins.
 */
export function startSpeakingSession() {
  speakingSession = true;
}

/**
 * Cancel any in-progress TTS playback and clear the queue.
 */
export function cancelTts() {
  if (currentSource) {
    try {
      currentSource.stop();
    } catch {
      // already stopped
    }
    currentSource = null;
  }
  audioQueue.length = 0;
  isPlaying = false;
  speakingSession = false;
  // Drain pending synthesis queue
  inflight = 0;
  synthQueue.length = 0;
}
