import { html } from 'lit';
import { ENGINES, ZONE_COLORS } from './constants.ts';

export type SignalEntry = {
  id: string;
  from: string;
  to: string;
  type: string;
  timestamp: number;
};

export function renderSignalLog(signals: SignalEntry[] | undefined) {
  if (!signals || signals.length === 0) {
    return html`<div class="muted" style="font-size:12px;">No recent signals</div>`;
  }

  const now = Date.now();
  const recent = signals.slice(0, 20);

  return html`
    <div class="mind-signals">
      ${recent.map(sig => {
        const age = now - sig.timestamp;
        const opacity = Math.max(0.3, 1 - age / 20000);
        const toEngine = ENGINES.find(e => e.id === sig.to);
        const fromEngine = ENGINES.find(e => e.id === sig.from);
        const color = toEngine ? ZONE_COLORS[toEngine.zone] : 'rgba(255,255,255,0.4)';

        return html`
          <div class="mind-signal" style="opacity:${opacity}">
            <span class="mind-signal__dot" style="background:${color}"></span>
            <span class="mind-signal__from">${fromEngine?.label ?? sig.from}</span>
            <span class="mind-signal__arrow">\u2192</span>
            <span class="mind-signal__to">${toEngine?.label ?? sig.to}</span>
            <span class="mind-signal__type">${sig.type}</span>
          </div>
        `;
      })}
    </div>
  `;
}
