const STORAGE_KEY = "wybe-mission";

export type GoalStatus = "active" | "paused" | "completed";

export type Goal = {
  id: string;
  title: string;
  description: string;
  agentId: string | null;
  dueDate: string | null;
  status: GoalStatus;
  progress: number;
  createdAt: number;
};

export type MissionData = {
  northStar: string;
  northStarProgress: number;
  goals: Goal[];
};

type GoalsHost = {
  missionGoals: Goal[];
  missionNorthStar: string;
  missionNorthStarProgress: number;
  missionGoalsLoading: boolean;
};

function defaultMissionData(): MissionData {
  return { northStar: "", northStarProgress: 0, goals: [] };
}

function readStorage(): MissionData {
  try {
    const raw = localStorage.getItem(STORAGE_KEY);
    if (!raw) return defaultMissionData();
    const parsed = JSON.parse(raw);
    return {
      northStar: parsed.northStar ?? "",
      northStarProgress: parsed.northStarProgress ?? 0,
      goals: Array.isArray(parsed.goals) ? parsed.goals : [],
    };
  } catch {
    return defaultMissionData();
  }
}

function writeStorage(data: MissionData) {
  localStorage.setItem(STORAGE_KEY, JSON.stringify(data));
}

export function loadGoals(host: GoalsHost) {
  host.missionGoalsLoading = true;
  const data = readStorage();
  host.missionNorthStar = data.northStar;
  host.missionNorthStarProgress = data.northStarProgress;
  host.missionGoals = data.goals;
  host.missionGoalsLoading = false;
}

function persist(host: GoalsHost) {
  writeStorage({
    northStar: host.missionNorthStar,
    northStarProgress: host.missionNorthStarProgress,
    goals: host.missionGoals,
  });
}

export function updateNorthStar(host: GoalsHost, text: string, progress: number) {
  host.missionNorthStar = text;
  host.missionNorthStarProgress = Math.max(0, Math.min(100, progress));
  persist(host);
}

export function addGoal(host: GoalsHost, goal: Omit<Goal, "id" | "createdAt">) {
  const newGoal: Goal = {
    ...goal,
    id: crypto.randomUUID(),
    createdAt: Date.now(),
  };
  host.missionGoals = [...host.missionGoals, newGoal];
  persist(host);
  return newGoal;
}

export function updateGoal(host: GoalsHost, id: string, patch: Partial<Goal>) {
  host.missionGoals = host.missionGoals.map((g) =>
    g.id === id ? { ...g, ...patch } : g,
  );
  persist(host);
}

export function removeGoal(host: GoalsHost, id: string) {
  host.missionGoals = host.missionGoals.filter((g) => g.id !== id);
  persist(host);
}
