import { html } from 'lit';

type FaceState = {
  valence?: number;
  arousal?: number;
  curiosity?: number;
  social?: number;
  confidence?: number;
  energy?: number;
};

export function renderFace(selfState: FaceState | null, blinkOpen = true) {
  const s = selfState ?? { valence: 0, arousal: 0.3, curiosity: 0.5, social: 0.3, confidence: 0.5, energy: 0.5 };
  const v = s.valence ?? 0;
  const a = s.arousal ?? 0.3;
  const c = s.curiosity ?? 0.5;
  const social = s.social ?? 0.3;

  const hue = 240 + v * 30;
  const eyeOpenY = blinkOpen ? 5 : 0.5;
  const pupilR = 1.5 + c * 1.5;
  const browRaise = (a + c) * 3;
  // Mouth: quadratic bezier curved by valence
  const mouthY = 68 + (1 - a) * 2;
  const mouthCurve = mouthY + v * 6; // positive = smile, negative = frown
  const cheekGlow = social > 0.5 ? (social - 0.5) * 0.4 : 0;

  const moodLabel = describeMood(v, a, c);

  return html`
    <svg viewBox="0 0 100 100" class="mind-face-svg" xmlns="http://www.w3.org/2000/svg">
      <defs>
        <radialGradient id="face-glow" cx="50%" cy="50%" r="50%">
          <stop offset="0%" stop-color="hsl(${hue}, 50%, 15%)" />
          <stop offset="100%" stop-color="transparent" />
        </radialGradient>
      </defs>

      <!-- Face outline -->
      <circle cx="50" cy="45" r="32" fill="url(#face-glow)"
              stroke="hsl(${hue}, 40%, 40%)" stroke-width="0.8" opacity="0.8" />

      <!-- Cheek glow -->
      ${cheekGlow > 0 ? html`
        <circle cx="35" cy="55" r="6" fill="hsl(350, 60%, 50%)" opacity="${cheekGlow}" />
        <circle cx="65" cy="55" r="6" fill="hsl(350, 60%, 50%)" opacity="${cheekGlow}" />
      ` : ''}

      <!-- Left eye -->
      <ellipse cx="40" cy="${48 - browRaise * 0.3}" rx="4" ry="${eyeOpenY}"
               fill="none" stroke="rgba(255,255,255,0.7)" stroke-width="0.8" />
      ${blinkOpen ? html`
        <circle cx="40" cy="${48 - browRaise * 0.3}" r="${pupilR}" fill="rgba(255,255,255,0.85)" />
      ` : ''}

      <!-- Right eye -->
      <ellipse cx="60" cy="${48 - browRaise * 0.3}" rx="4" ry="${eyeOpenY}"
               fill="none" stroke="rgba(255,255,255,0.7)" stroke-width="0.8" />
      ${blinkOpen ? html`
        <circle cx="60" cy="${48 - browRaise * 0.3}" r="${pupilR}" fill="rgba(255,255,255,0.85)" />
      ` : ''}

      <!-- Eyebrows -->
      <line x1="35" y1="${41 - browRaise}" x2="45" y2="${40 - browRaise * 1.2}"
            stroke="rgba(255,255,255,0.4)" stroke-width="0.6" stroke-linecap="round" />
      <line x1="55" y1="${40 - browRaise * 1.2}" x2="65" y2="${41 - browRaise}"
            stroke="rgba(255,255,255,0.4)" stroke-width="0.6" stroke-linecap="round" />

      <!-- Mouth -->
      <path d="M 40 ${mouthY} Q 50 ${mouthCurve} 60 ${mouthY}"
            fill="none" stroke="rgba(255,255,255,0.6)" stroke-width="0.8" stroke-linecap="round" />

      <!-- Mood label -->
      <text x="50" y="90" text-anchor="middle" fill="rgba(255,255,255,0.4)"
            font-size="6" font-family="var(--mono)">
        ${moodLabel}
      </text>
    </svg>
  `;
}

export function renderMiniFace(selfState: FaceState | null) {
  const s = selfState ?? { valence: 0, arousal: 0.3, curiosity: 0.5 };
  const v = s.valence ?? 0;
  const hue = 240 + v * 30;
  const mouthCurve = 21 + v * 3;

  return html`
    <svg viewBox="0 0 30 30" width="30" height="30" xmlns="http://www.w3.org/2000/svg">
      <circle cx="15" cy="13" r="10" fill="none" stroke="hsl(${hue}, 40%, 40%)" stroke-width="0.6" opacity="0.6" />
      <circle cx="12" cy="11" r="1.2" fill="rgba(255,255,255,0.7)" />
      <circle cx="18" cy="11" r="1.2" fill="rgba(255,255,255,0.7)" />
      <path d="M 11 ${18} Q 15 ${mouthCurve} 19 ${18}" fill="none" stroke="rgba(255,255,255,0.5)" stroke-width="0.6" />
    </svg>
  `;
}

function describeMood(valence: number, arousal: number, curiosity: number): string {
  const parts: string[] = [];
  if (curiosity > 0.6) parts.push('Curious');
  if (valence > 0.3) parts.push('Positive');
  else if (valence < -0.3) parts.push('Troubled');
  if (arousal > 0.6) parts.push('Alert');
  else if (arousal < 0.2) parts.push('Calm');
  if (parts.length === 0) parts.push('Neutral');
  return parts.join(' & ');
}
